.header{
    height: 34vw ;
    margin: 30px auto;
    background:url('C:\Users\maku2\Documents\Placement\MERN\Food Delivery\fooddelivery\public\header_img.jpeg') no-repeat;
    background-size: cover;
    border-radius: 10px;
    position: relative;
    /* opacity: 0.8; */
}

.header-contents{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap:1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s ;
    
}

.header-contents h2{
    font-weight: 500;
    color: #FFD700 ;
    font-size: max(4.5vw, 22px);
}

.header-contents p{
    color: #333333 ;
    font-size: 15px;
}

.header-contents button{
    border: none;
    color:#747474 ;
    font-weight: 500;
    padding: 1vw 2.3vw;
    background-color: white;
    font-size: max(1vw, 13px);
    border-radius: 50px;
    cursor: pointer;
}

@media (max-width:1050px){
    /* .header h2{
        font-size: (3.5vw, 20px)
    } */

    .header{
        height: 40vw;
    }
   
    .header-contents{
        max-width: 45%;
    }
}


@media (max-width:750px){
   
    .header-contents{
        max-width: 65%;
    }

    .header-contents p{
        display: none;
    }

    .header-contents button{
        padding: 2vw 4vw;
    }
}

